import angular from 'angular'

export default angular
  .module('SocialSignOnControllerModule', [])
  .controller(
    'SocialSignOnController',
    function ($filter, $scope, $stateParams, $window, config, lang) {
      $scope.config = config
      $scope.lang = lang

      $scope.redirect = $stateParams && $stateParams.redirect

      $scope.forgotPasswordRedirect = () => {
        $window.location.href =
          config.externalForgotPasswordHref || $filter('rootHref')('/forgot-password')
      }
    }
  )
